
import {AdvancedFilterOption, NumberOrBigNumber} from './advanced-filter/advanced-filter.models';
import BigNumber from 'bignumber.js';

export const ADVANCED_FILTER_CONSTS = {
  TAG_OPTION_DELIMITER: ':'
};

// TODO: Refactor this if possible
/* eslint-disable-next-line @typescript-eslint/no-explicit-any */
export function formatAdvancedFilterTagText(filterOption: AdvancedFilterOption, displayValue: any): string {
  return `${filterOption.label}${ADVANCED_FILTER_CONSTS.TAG_OPTION_DELIMITER} ${displayValue}`;
}

export function isFiniteNumber(value: NumberOrBigNumber): value is NumberOrBigNumber {
  return BigNumber.isBigNumber(value) || (typeof value === 'number' && isFinite(value));
}

export function parseNumericRange(str: string): NumberOrBigNumber[] {
  const numbersValues: number[] = [];

  const regexp = /[+-]?\d+(\.\d+)?/gi;
  /* eslint-disable */
  let regexpResult: RegExpExecArray;

  while (regexpResult = regexp.exec(str)) {
    const valueNumber = parseFloat(regexpResult[0]);

    if (isFiniteNumber(valueNumber)) {
      numbersValues.push(valueNumber);
    }
  }
  /* eslint-enable */

  return numbersValues;
}
